@font-face {
  font-family: "neuzeit-s-book";
  src: marketing-font-url("neuzeit-s-book.eot");
  src: marketing-font-url("neuzeit-s-book.eot?#iefix") format("embedded-opentype"),
       marketing-font-url("neuzeit-s-book.woff") format("woff"),
       marketing-font-url("neuzeit-s-book.ttf") format("truetype");
  font-weight:normal;
  font-style:normal;
  font-variant:normal;
}

